<template>
  <div>
    <van-nav-bar title="密码详情" :left-arrow="true" fixed @click-left="onClickLeft"/>
    <form action="javascript:void(0)" method="post">
      <van-cell-group>
        <van-field
          v-model="dataList.username"
          border
          placeholder="请输入名称"
          label="名称"
          required
        />
        <van-field
          v-model="dataList.password"
          border
          placeholder="请输入密码"
          label="密码"
          required
        />
        <van-field
          v-model="dataList.instructions"
          border
          placeholder="请输入密码所属"
          disabled
          label="所属"
          required
        />
        <van-field
          readonly
          v-model="dataList.Groups[0].gName"
          clickable
          label="分类"
          required
          placeholder="选择分类"
          @click="showPicker = true"
        />
      </van-cell-group>

      <van-button type="primary" block style="margin-top:10px" disabled>修改</van-button>

      <van-popup v-model="showPicker" position="bottom">
        <van-picker
          show-toolbar
          :columns="mainGroupList"
          @cancel="showPicker = false"
          @confirm="onConfirm"
        />
      </van-popup>
    </form>
    <van-loading type="spinner" class="loading-pos" color="#1989fa" v-if="loading"/>
  </div>
</template>

<script>
/* eslint-disable */
import { getInfoById, getAllGroup } from "@/api/api.js";
export default {
  data() {
    return {
      dataList: {},
      showPicker: false,
      loading:false
    };
  },
  computed: {
    infoId() {
      return this.$route.query.id;
    }
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    }
  },
  created() {
    this.loading = true;
    getInfoById(this.$route.query.id).then(res => {
        this.loading = false;
      if (res.code == "success") {
        this.dataList = res.message[0];
      }
    }).catch(err=>{
        this.loading = false;
        this.$toast.fail(err);
    });
    getAllGroup()
      .then(res => {
        if (res.code == "success") {
          var list = res.message.map(item => {
            return item.gName;
          });
          this.mainGroupList = list;
        }
      })
      .catch(err => {
        this.$toast.fail(err);
      });
  }
};
</script>

